.reviews_list {
    list-style: none;
}

.reviews_item {
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 0px 0px 4px 4px;
    padding: 10px;
    margin-bottom: 10px;
}

.reviews_author {
    font-size: 22px;
}

.reviews_content {
    font-size: 20px;
}